import {Table,TableCell,TableRow,TableHead,TableBody,Button} from '@material-ui/core'; 
import {useState,useEffect} from 'react';
import { getallData } from '../Service/api';

const TableData=()=>{
    
    const [data,setData]=useState([]);

    const fetchData=async ()=>{
        const res=await getallData();
        setData(res.data);
        console.log(res.data);
    }

    useEffect(()=>{    
    fetchData();        
    },[]);

    return(
        <>
            <h1>
            <Table>
                <TableHead>
                    <TableRow>
                    <TableCell>Start Year</TableCell>
                    <TableCell>End Year</TableCell>
                    <TableCell>Intensity</TableCell>
                    <TableCell>Topic</TableCell>
                    <TableCell>Insight</TableCell>
                    <TableCell>Region</TableCell>
                    <TableCell>Sector</TableCell>
                    <TableCell>Url</TableCell>
                    <TableCell>Relevance</TableCell>
                    <TableCell>Pestle</TableCell>
                    <TableCell>Source</TableCell>
                    <TableCell>Title</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {data.map(row=>{
                        return(
                            <>
                                <TableRow>
                                    <TableCell>{row.start_year}</TableCell>
                                    <TableCell>{row.end_year}</TableCell>
                                    <TableCell>{row.intensity}</TableCell>
                                    <TableCell>{row.topic}</TableCell>
                                    <TableCell>{row.insight}</TableCell>
                                    <TableCell>{row.region}</TableCell>
                                    <TableCell>{row.sector}</TableCell>
                                    <TableCell><Button variant="contained" color="primary" href={row.url}>Link</Button></TableCell>
                                    <TableCell>{row.relevance}</TableCell>
                                    <TableCell>{row.pestle}</TableCell>
                                    <TableCell>{row.source}</TableCell>
                                    <TableCell>{row.title}</TableCell>
                                </TableRow>
                            </>
                        )
                    })}
                </TableBody>
            </Table>
            </h1>
        </>
    )
}

export default TableData;