import {useEffect,useState} from 'react';
import {getData} from '../Service/api';
import {Bar,Radar,Pie,Line} from 'react-chartjs-2';
import {Button,makeStyles,Typography} from '@material-ui/core';
import { FormControl,InputLabel,Input} from '@mui/material'

const styles={
    line:{
        display:"flex",
        flexDirection:"row"
    }
}

const useStyles=makeStyles({
    tabs:{
        marginTop:"5px",
        marginRight:"10px"
    }
})

const initial_value={
    intensity:true,
    likelihood:true,
    relevance:true
}

const initial_chart={
    pie:false,
    bar:false,
    radar:false,
    line:false
}

const initial_filter={
    startYear:0,
    endYear:10000
}

const Topicvis=()=>{
    const classes=useStyles();

    const [data,setData]=useState([]);
    const [topics,settopics]=useState({});
    const [intensity_topics,setintensity]=useState({});
    const [likelihood,setlikelihood]=useState({});
    const [relevance,setRelevance]=useState({});
    const [flags,setflags]=useState(initial_value);
    const [charts,setcharts]=useState(initial_chart);
    const [filter,setfilter]=useState(initial_filter);

    const get_Data=async()=>{
        const res=await getData(filter);
        const topics={};
        const intensities={};
        const likelihood={};
        const relevance={};
        
        setData(res.data);
        res.data.filter(row=>row.intensity!="" & row.topic!="" & row.likelihood!="" & row.country!="" & row.region!="").forEach(row=>{
            topics[row.region]=1;
            intensities[row.region]=0;
            likelihood[row.region]=0;
            relevance[row.region]=0;

        });

        res.data.filter(row=>row.intensity!="" & row.topic!="" & row.likelihood!="" & row.country!="" & row.region!="").forEach(row=>{
            topics[row.region]++;
            intensities[row.region]+=parseInt(row.intensity);
            likelihood[row.region]+=parseInt(row.likelihood);
            relevance[row.region]+=parseInt(row.relevance);

        });

        settopics(topics);
        setintensity(intensities);
        setlikelihood(likelihood);
        setRelevance(relevance);

    }

    useEffect(()=>{
    get_Data();
    setcharts({...initial_chart,line:true})
    },[]);

    const toggleval=(e)=>{
        setflags({...flags,[e.currentTarget.name]:!flags[e.currentTarget.name]});
    }

    const graph_data=(e)=>{
        const dataset=[];
        if(flags.intensity)
        dataset.push({
            label:"Average Intensities",
            data:Object.keys(topics).map(key=>intensity_topics[key]/topics[key]),
            backgroundColor:(e=='pie') ? 'rgba(153, 255, 153,0)' : 'rgba(153, 255, 153,1)',
            borderColor:'rgba(153, 255, 153,1)',
            fill:false
        });
        if(flags.likelihood)
        dataset.push({
            label:"Average LikeliHood",
            data:Object.keys(topics).map(key=>likelihood[key]/topics[key]),
            backgroundColor:(e=='pie') ? 'rgba(153, 255, 153,0)' :'rgba(54, 162, 235, 1)',
            borderColor:'rgba(54, 162, 235, 1)',
            fill:false
        });
        if(flags.relevance)
        dataset.push({
            label:"Average Relevance",
            data:Object.keys(topics).map(key=>relevance[key]/topics[key]),
            backgroundColor:(e=='pie') ? 'rgba(153, 255, 153,0)' :'rgba(255, 153, 51,1)',
            borderColor:'rgba(255, 153, 51,1)',
            fill:false 
        })

        return dataset;
    
    }

    const getChart=()=>{
        if(charts.bar)
        return(
        <Bar height={600} width={600} data={{
            labels:Object.keys(topics)
            ,
            datasets:graph_data()            
        }}
        options={{maintainAspectRatio:false}}>
        </Bar>
        )
        if(charts.line)
        return(
            <Line height={600} width={600} data={{
                labels:Object.keys(topics)
                ,
                datasets:graph_data()            
            }}
            options={{maintainAspectRatio:false}}>
            </Line>
        )
        if(charts.radar)
        return(
            <Radar height={600} width={600} data={{
                labels:Object.keys(topics)
                ,
                datasets:graph_data()            
            }}
            options={{maintainAspectRatio:false}}>
            </Radar>
        )
        if(charts.pie)
        return(
            <Pie height={600} width={600} data={{
                labels:Object.keys(topics)
                ,
                datasets:graph_data('pie')          
            }}
            options={{maintainAspectRatio:false}}>
            </Pie>
        )

    }

    const setChart=(e)=>{
        setcharts({...initial_chart,[e.currentTarget.name]:true});
    }

    const ApplyFilter=(e)=>{
        e.preventDefault();
        get_Data();
    }

    const changeFilter=(e)=>{
        setfilter({...filter,[e.target.name]:e.target.value});
    }
    
    return(
    <>    
    <div style={styles.line}>
        <div style={{flex:"1",marginTop:"50px",display:"flex",flexDirection:"column",marginLeft:"20px"}}>
        <div>
        <Typography variant="h4" >Options</Typography>
            <Button name="intensity" className={classes.tabs} variant ="contained" color="primary" onClick={(e)=>{
                toggleval(e);
            }}>{flags.intensity==false ? "Show Intensity":"Hide Intensity" }</Button><br/>
            
            <Button name="likelihood" className={classes.tabs} variant ="contained" color="primary" onClick={(e)=>{
                toggleval(e);
            }}>{flags.likelihood==false ? "Show LikeliHood":"Hide Likelihood" }</Button><br/>
            
            <Button name="relevance" className={classes.tabs} variant ="contained" color="primary" onClick={(e)=>{
                toggleval(e);
            }}>{flags.relevance==false ? "Show Relevance":"Hide Relevance" }</Button>
        </div>
        <div>
        <br/>
        <Typography variant="h4" >Charts</Typography>
        <Button className={classes.tabs} variant="contained" color="primary" name="bar" onClick={(e)=>setChart(e)}>Bar</Button>
        <Button className={classes.tabs} variant="contained" color="primary" name="pie" onClick={(e)=>setChart(e)}>Pie</Button><br/>
        <Button className={classes.tabs} variant="contained" color="primary" name="radar" onClick={(e)=>setChart(e)}>Radar</Button>
        <Button className={classes.tabs} variant="contained" color="primary" name="line" onClick={(e)=>setChart(e)}>Line</Button>
        </div>
        <div>
        <br/>
        <Typography variant="h4" >Filters</Typography>
        <FormControl>
        <InputLabel htmlFor="my-input"  >Start Year</InputLabel>
        <Input id="my-input" aria-describedby="my-helper-text" name="startYear" onChange={e=>changeFilter(e)} />
        </FormControl>
        <FormControl>
        <InputLabel htmlFor="my-input"  >End Year</InputLabel>
        <Input id="my-input" aria-describedby="my-helper-text" name="endYear" onChange={e=>changeFilter(e)} />
        </FormControl>
        <br/>
        <Button className={classes.tabs} variant="contained" color="primary" name="bar" onClick={(e)=>ApplyFilter(e)}>Apply Filter</Button>
        </div>
        </div>
        <div style={{flex:"3", marginRight:"20px"}}>
        {getChart()}
        </div>
    </div>
    </>
    )
}

export default Topicvis;