import {makeStyles,AppBar,Toolbar,Typography} from '@material-ui/core';
import {Link} from 'react-router-dom';

const useStyles=makeStyles({
    header:{
    },
    tabs:{
        color:"white",
        textDecoration:"none"
    }
})
const Header=()=>{
    

    const classes=useStyles();
    return(
        <>
            <AppBar position='static' className={classes.header}>
                <Toolbar>
                    <Typography className={classes.tabs} component={Link} to="/table">Table Data</Typography>&nbsp;&nbsp;&nbsp;&nbsp;
                    <Typography className={classes.tabs} component={Link} to="/topic">Topics</Typography>&nbsp;&nbsp;&nbsp;&nbsp;
                    <Typography className={classes.tabs} component={Link} to="/country">Countries</Typography>&nbsp;&nbsp;&nbsp;&nbsp;
                    <Typography className={classes.tabs} component={Link} to="/region">Regions</Typography>&nbsp;&nbsp;&nbsp;&nbsp;
                </Toolbar>
            </AppBar>
        </>
    )
};

export default Header;